.section-four-exercise{
  display: none;
}

.gallery{
  margin-top:20px;
}

.gallery img{
  width:123px;
  height: 120px;
  border:3px solid #bfbfbf;
  transform: rotate(-6deg);

}

.gallery img:nth-child(odd){
  transform: scale(1.2) rotate(5deg);
}

.questions{
  margin:30px;
}

.box{
  font-size: 20px;
  margin-top:10px;
}

.question{
  font-size:22px;
  margin-bottom:15px;
}

label{
  margin-top:10px;
  font-weight: 500;
}

.answers input{
  margin-right:5px;
}

@media(max-width:1000px){
  .btn-prev{
    font-size: 13px;
    padding: 5px 12px;
  }
  .gallery img{
    width:60px;
    height:60px;
  }
}

@media (max-width:500px){
  .question, .box{
    font-size:15px;
  }
}

